.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 50px;
}

.container .button {
  text-align: right;
}

h2 {
  text-align: center;
}

.registration-flex, .registration-flex-error {
  display: flex;
  justify-content: center;
  align-items: center;
}

.registration-flex .registration-flex-row, .registration-flex .registration-flex-error-row, .registration-flex-error .registration-flex-row, .registration-flex-error .registration-flex-error-row {
  width: 500px;
}

.registration-flex .registration-flex-row .alert-danger, .registration-flex .registration-flex-error-row .alert-danger, .registration-flex-error .registration-flex-row .alert-danger, .registration-flex-error .registration-flex-error-row .alert-danger {
  background-color: transparent;
  margin-left: 3px;
}

.registration-flex .registration-flex-row .error-label, .registration-flex .registration-flex-error-row .error-label, .registration-flex-error .registration-flex-row .error-label, .registration-flex-error .registration-flex-error-row .error-label {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.registration-form .membership-label {
  display: flex;
  align-items: center;
  margin-top: 15px;
}

.registration-form .btn-membership {
  background-color: rgba(0, 0, 0, 0.3);
  border-color: rgba(0, 0, 0, 0.3);
  font-weight: bold;
  font-size: 18px;
}

.registration-form .btn-membership:hover {
  background-color: #C79F37;
  border-color: #C79F37;
}

.registration-form .btn-membership.focus {
  background-color: #C79F37;
  border-color: #C79F37;
}

.registration-form .label {
  margin: 15px 0px 3px;
}

.package-form {
  width: 100%;
  display: none;
}

.package-form th, .package-form td {
  text-align: center;
}

.package-form .green {
  color: green;
}

.package-form .package-form-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25px;
}

@media (min-width: 1000px) {
  .registration-flex-error {
    display: none;
  }
  .registration-form {
    width: 500px;
  }
  .registration-form .btn-membership {
    width: 250px;
  }
}

@media (max-width: 1000px) {
  .registration-flex-row {
    display: none;
  }
}

@media (max-width: 1000px) and (min-width: 412px) {
  .registration-form, .registration-flex-error {
    width: 300px;
  }
  .registration-form .btn-membership, .registration-flex-error .btn-membership {
    width: 150px;
  }
}

@media (max-width: 1000px) and (max-width: 411px) {
  .registration-form, .registration-flex-error {
    width: 200px;
  }
  .registration-form .btn-membership, .registration-flex-error .btn-membership {
    width: 100px;
  }
}
